@import '../../App.scss';

.product-item{
    background-color: $clr-white;
    position: relative;
    border-radius: 8px;
    box-shadow: rgba(50, 50, 93, 0.05) 0px 2px 5px -1px, rgba(0, 0, 0, 0.05) 0px 1px 3px -1px;
    transition: $transition-ease;
    .category{
        position: absolute;
        left: -5px;
        top: 1.6rem;
        background: $clr-orange;
        color: $clr-white;
        font-size: 13px;
        text-transform: capitalize;
        padding: 0.2rem 1rem;
        box-shadow: rgba(0, 0, 0, 0.05) 0px 1px 2px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
       
        &::after{
            position: absolute;
            content: "";
            left: 0;
            top: 100%;
            transform: translateY(-5px);
            width: 0;
            height: 0;
            border-top: 5px solid transparent;
            border-bottom: 5px solid transparent;
            border-right: 5px solid $clr-orange;
        }
    }
    .product-item-img{
        padding-bottom: 4px;
        margin-bottom: 12px;
        height: 250px;
        overflow: hidden!important;
        border-top-left-radius: 8px;
        border-top-right-radius: 8px;
    }
    .product-item-info{
        padding: 0 12px 20px 12px;
        text-align: center;
        font-family: $font-family-manrope;
        opacity: 0.8;
        .brand{
            border-bottom: 1px solid rgba(0, 0, 0, 0.05);
            padding-bottom: 0.6rem;
            span{
                &:first-child{
                    margin-right: 3px;
                }
            }
        }
        .product-title{
            font-size: 14.5px;
            font-weight: 500;
            letter-spacing: 0.3px;
        }
        .product-price{
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
            .old-price{
                opacity: 0.7;
                text-decoration: line-through;
                font-size: 12px;
            }
            .new-price{
                margin: 0 1rem;
                font-weight: 700;
                font-size: 16px;
            }
            .discount{
                font-size: 13px;
                font-weight: 600;
                font-family: $font-family-poppins;
                color: darken($clr-orange, 5%);
            }

            &::after{
                content: "";
                position: absolute;
                top: calc(100% + 6px);
                height: 1px;
                background-color: lighten($clr-orange, 10%);
                width: 70px;
                margin-left: 16px;
                
            }
        }
    }
    
}
.category:hover{
        box-shadow: rgba(50, 50, 93, 0.1) 0px 2px 5px -1px, rgba(0, 0, 0, 0.1) 0px 1px 20px -1px;
    }